*, h1, h2, p {
  margin: 0;
  padding: 0;
}

body {
  padding-left: 40px;
  padding-right: 40px;
  overflow-x: hidden;
}

.select-container {
  margin-top: 20px;
}

.breed-select {
  width: 150px;
  margin-bottom: 10px;
  padding: 4px;
}

.box {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.cat-info {
  gap: 30px;
  display: flex;
}

.text-container {
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  display: flex;
}

.title, .text, .subtitle {
  color: #22343d;
  font-family: sans-serif;
  font-size: 24px;
  line-height: normal;
}

.title {
  font-weight: 700;
}

.text {
  font-size: 16px;
}

.subtitle {
  font-size: 22px;
  font-weight: 500;
}

.image {
  box-shadow: 1.5px 1.2px 1px 1px rgba(34, 52, 61, .667);
}

.loader {
  font-size: 18px;
  font-weight: 600;
}

.error {
  font-weight: 16px;
  color: red;
  font-weight: 500;
}

.is-hidden {
  display: none;
}

.loader {
  width: 96px;
  box-sizing: content-box;
  height: 48px;
  background: #fff;
  border: 2px solid #de3500;
  border-bottom-width: 50px;
  border-radius: 100%;
  animation: yinYang 3s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #fff;
  border: 18px solid #de3500;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 0;
}

.loader:after {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #de3500;
  border: 18px solid #fff;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes yinYang {
  100% {
    transform: rotate(360deg);
  }
}

